import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faPlay, faPause, faAngleLeft, faAngleRight} from '@fortawesome/free-solid-svg-icons';
const Player = ({currentSong, isPlaying, setisPlaying, audioRef, songInfo, setsongInfo, playSonghandler, songs, setcurrentSong, setsongs}) => {
    
    // useEffect(() => {
    //     const newSongs = songs.map((song) => {
    //         if (song.id === currentSong.id) {
    //             return {
    //                 ...song,
    //                 active: true,
    //             }
    //         } else {
    //             return {
    //                 ...song,
    //                 active: false,
    //             }
    //         }
    //     })

    //     setsongs(newSongs)
    // }, [currentSong.id])
    const getTime = (time) => {
        return (
            Math.floor(time / 60) + ":" + ("0" + Math.floor(time % 60)).slice(-2)
        );
    }

    const skipTrackHandler = async (direction) => {
        let currentIndex = songs.findIndex((song) => song.id === currentSong.id);
        if (direction === 'skip-forward') {
            
            await setcurrentSong(songs[(currentIndex + 1) % songs.length]);

            const newSongs = songs.map((song) => {
                if (song.id === (songs[(currentIndex + 1) % songs.length]).id) {
                    return {
                        ...song,
                        active: true,
                    }
                } else {
                    return {
                        ...song,
                        active: false,
                    }
                }
            })
    
            setsongs(newSongs)

        } else if (direction === 'skip-back') {
            if ((currentIndex -1) % songs.length === -1) {
            await setcurrentSong(songs[songs.length -1]);
            if (isPlaying) {
                audioRef.current.play();
            }   

            const newSongs = songs.map((song) => {
                if (song.id === (songs[songs.length -1]).id) {
                    return {
                        ...song,
                        active: true,
                    }
                } else {
                    return {
                        ...song,
                        active: false,
                    }
                }
            })
    
            setsongs(newSongs)
                return;
            }
            await setcurrentSong(songs[(currentIndex - 1) % songs.length]);
            const newSongs = songs.map((song) => {
                if (song.id === (songs[(currentIndex - 1) % songs.length]).id) {
                    return {
                        ...song,
                        active: true,
                    }
                } else {
                    return {
                        ...song,
                        active: false,
                    }
                }
            })
    
            setsongs(newSongs)
        }

        if (isPlaying) {
                    audioRef.current.play();
        }
        


    }

    const draghandler = (e) => {
        audioRef.current.currentTime = e.target.value
        setsongInfo({...songInfo, currentTime: e.target.value})
    }

    document.body.onkeyup = function(e){
        if(e.keyCode === 32){
            playSonghandler()
        }
    }
    return (
        <React.Fragment>
        <div className='player'>
            <div className="time-control">
                <p>{getTime(songInfo.currentTime)}</p>
                <input min={0} max={songInfo.duration || 0} value={songInfo.currentTime} onChange={draghandler} type="range" />
                <p>{songInfo.duration ? getTime(songInfo.duration) : '0:00'}</p>
            </div>

            <div className="play-control">
            <FontAwesomeIcon icon={faAngleLeft} className='skip-back' onClick={() => skipTrackHandler('skip-back')} size="2x" />
            <FontAwesomeIcon onClick={playSonghandler} icon={isPlaying ? faPause : faPlay} className='play' size="2x" />
            <FontAwesomeIcon icon={faAngleRight} className='skip-forward' onClick={() => skipTrackHandler('skip-forward')} size="2x" />
            </div>

            
        </div>
        <p className='devBy'>Developed By: <span>Okoye Udoka</span></p>
        </React.Fragment>
    )
}

export default Player
