import React, {useState, useRef} from 'react';
import './Styles/app.css'
import Song from './Components/Song';
import Player from './Components/Player';
import Libary from './Components/Libary'
import Nav from './Components/Nav';
import data from './util';
function App() {
  const [songs, setsongs] = useState(data());
  const [currentSong, setcurrentSong] = useState(songs[0]);
  const [isPlaying, setisPlaying] = useState(false);
  const [libaryStatus, setlibaryStatus] = useState(false);
  const audioRef = useRef(null);

  const [songInfo, setsongInfo] = useState({
    currentTime: 0,
    duration: 0
})

const playSonghandler = () => {
  if (isPlaying) {
      setisPlaying(!isPlaying)
      audioRef.current.pause();
  } else {
      audioRef.current.play();
      setisPlaying(!isPlaying)
  }
}

  const timeUpdatehandler = async (e) => {
    const current = e.target.currentTime;
    const duration = e.target.duration;
    setsongInfo({...songInfo, currentTime: current, duration})

    if (current === duration) {
      let currentIndex = songs.findIndex((song) => song.id === currentSong.id);
      await setcurrentSong(songs[(currentIndex + 1) % songs.length]);
      if (isPlaying) {
        audioRef.current.play();
    }
    }
}
  return (
    <div className={`App ${libaryStatus ? "libary-active" : ''}`}>
      <Nav libaryStatus={libaryStatus} setlibaryStatus={setlibaryStatus} />
      <Song currentSong={currentSong} />
      <Player setsongs={setsongs} setcurrentSong={setcurrentSong} songs={songs} playSonghandler={playSonghandler} setsongInfo={setsongInfo} songInfo={songInfo} audioRef={audioRef} currentSong={currentSong} setisPlaying={setisPlaying} isPlaying={isPlaying} />

      <Libary setlibaryStatus={setlibaryStatus} libaryStatus={libaryStatus} setsongs={setsongs} isPlaying={isPlaying} playSonghandler={playSonghandler} songs={songs} currentSong={currentSong} audioRef={audioRef} setcurrentSong={setcurrentSong}/>
      <audio onTimeUpdate={timeUpdatehandler} onLoadedMetadata={timeUpdatehandler} ref={audioRef} src={currentSong.audio}></audio>
    </div>
  );
}

export default App;
