import React from 'react'
import LibarySong from './LibarySong'
const Libary = ({songs, setcurrentSong, playSonghandler, audioRef, isPlaying, setsongs, libaryStatus, setlibaryStatus}) => {
    return (
        <div className={`libary ${libaryStatus ? 'active-libary' : ''}`}>
            <h2>Libary</h2>
            <div className="libary-songs">
                {songs.map((song) =>  (

                <LibarySong setlibaryStatus={setlibaryStatus} libaryStatus={libaryStatus} setsongs={setsongs} isPlaying={isPlaying} playSonghandler={playSonghandler} audioRef={audioRef} songs={songs} song={song} setcurrentSong={setcurrentSong} id={song.id} key={song.id} />

                ))}
            </div>
        </div>
    )
}

export default Libary
