import React from 'react'

const LibarySong = ({song, setcurrentSong, songs, id, playSonghandler, audioRef, isPlaying, setsongs, setlibaryStatus}) => {

    const songSelectHandler = () => {
        setlibaryStatus(false);
        audioRef.current.play();
        setcurrentSong(song)

        const newSongs = songs.map((song) => {
            if (song.id === id) {
                return {
                    ...song,
                    active: true,
                }
            } else {
                return {
                    ...song,
                    active: false,
                }
            }
        })

        setsongs(newSongs)

        if (isPlaying) {
            const playPromise = audioRef.current.play();

            if (playPromise !== undefined) {
                playPromise.then((audio) => {
                    audioRef.current.play();
                })
            }
        }
    }
    return (
        <div onClick={songSelectHandler} className={`libary-song ${song.active ? 'selected' : ''}`}>
            <div className="img">
            <img src={song.cover} alt="Song Cover"/>
            </div>
            <div className="song-description">
            <h3>{song.name}</h3>
            <h4>{song.artist}</h4>
            </div>

        </div>
    )
}

export default LibarySong
